/*******BUTTONS**********//
button:focus {outline:0;}

.btn-all{
  position: relative;
  font-family: 'Abel', sans-serif;
  background: none;
  display: block;
  border: none;
  -webkit-transition: 0.2s ease;
  -o-transition: 0.2s ease;
  transition: 0.2s ease;
  cursor: pointer;
  font-weight: 700;
  outline: none!important;
  text-decoration: none;
  text-align: center;
}

.btn-big{

  margin: auto;
  margin-top: 4%;
  color: #375b99;
  background-color: #efefef;
  font-weight: 700;
  font-size: 1.4em;
}

.btn-big:hover{

  color: white;
  background-color: #375b99;
}

.form-btn{

  height: 2em;
}

.btn-go-back {
  position: absolute;
  right: 0;
  top: 0;
  text-align: center;
  color: #375b99;
  font-size: 0.5em;
  width: 10%;
}

.btn-cover{
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background: none;
  border: none;
  outline: none;
}
.div-cover{
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
}


