/* Animations*/
@keyframes fadeInUp{
  from {
    opacity: 0;
    bottom: 0%;

  }

  to {
    opacity: 1;
    bottom: 25%;

  }

}

@-webkit-keyframes fadeInDownMenu{
  from {
    position: absolute;
    opacity: 0;
    top: -100%;
    right: 0;
  }
  to {
    position: absolute;
    opacity: 1;
    top: 0%;
    right: 0;
  }
}

@keyframes fadeInDownMenu{
  from {
    position: absolute;
    opacity: 0;
    top: -100%;
    right: 0;
  }
  to {
    position: absolute;
    opacity: 1;
    top: 0%;
    right: 0;
  }
}

@-webkit-keyframes fadeInDownMessage{
  from {
    opacity: 0;
    top: -10%;
  }
  to {
    opacity: 1;
    top: 2%;
  }
}

@keyframes fadeInDownMessage{
  from {
    opacity: 0;
    top: -10%;
  }
  to {
    opacity: 1;
    top: 2%;
  }
}



